var vstruct = require('varstruct');

var _require = require('deterministic-json'),
    stringify = _require.stringify,
    parse = _require.parse;

var TxStruct = vstruct([{
  name: 'data',
  type: vstruct.VarString(vstruct.UInt32BE)
}, {
  name: 'nonce',
  type: vstruct.UInt32BE
}]);

exports.decode = function (txBuffer) {
  var decoded = TxStruct.decode(txBuffer);
  var tx = parse(decoded.data);
  return tx;
};

exports.encode = function (txData, nonce) {
  var data = stringify(txData);
  var bytes = TxStruct.encode({
    nonce: nonce,
    data: data
  });
  return bytes;
};